import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class NavBar extends Component {
  render() {
    return (
      <div>
        <ul className='navbar'>
          <Link to='/shineshare' className='navbar-item'>Home</Link>
          <Link to='/shineshare/pokemon' className='navbar-item'>Pokemon</Link>
          <Link to='/shineshare/community' className='navbar-item'>Community</Link>
          <Link to='/shineshare/about' className='navbar-item'>About</Link>
          <Link to='/shineshare/support' className='navbar-item'>Support</Link>
        </ul>
      </div>
    )
  }
}

export default NavBar;